<template>
  <div class="container login">
    <div class="title">
      <h3>ÊàëÁöÑÂ≠¶‰π†‰πêÂõ≠</h3>
    </div>
    <p class="login-error" v-show="hasError">
      {{ loginErrMsg }}
    </p>
    <p>
      <input
        type="text"
        class="user-name"
        placeholder="Áî®Êà∑Âêç"
        v-model="userName"
      />
    </p>
    <p>
      <input
        type="password"
        class="password"
        placeholder="ÂØÜÁ†Å"
        v-model="password"
      />
    </p>
    <input type="button" value="ÁôªÂΩï" class="btn btn-login" @click="login" />
    <button class="forget-pwd" @click="forgetPwd"><u>ÊàëÂøòËÆ∞‰∫ÜÂØÜÁ†Å</u></button>
    <p class="forget-pwd-content" v-show="forgetPwdFlag">
      Â¶ÇÊûúÂøòËÆ∞ÂØÜÁ†ÅËØ∑ËÅîÁ≥ª‰Ω†ÁöÑÁà∏Áà∏ÊàñËÄÖÂ¶àÂ¶àËøõË°åËß£ÈîÅÔºåÊØèÊ¨°Ëß£ÈîÅË¥πÁî®<b>{{
        resetPwdPrice
      }}</b
      >ÂÖÉÔºÅÔºÅÔºÅ
    </p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userName: "",
      password: "",
      forgetPwdFlag: false,
      hasError: false,
      resetPwdPrice: 2,
      loginErrMsg: "Áî®Êà∑ÂêçÊàñÂØÜÁ†ÅÈîôËØØÔºåËØ∑ÈáçÊñ∞ËæìÂÖ•",
    };
  },
  methods: {
    forgetPwd() {
      this.forgetPwdFlag = !this.forgetPwdFlag;
    },
    login() {
      this.hasError = false;
      if (this.userName != "Âº†" || this.password != "7070") {
        this.hasError = true;
        return;
      }
      // Ë∑≥ËΩ¨Âà∞topÁîªÈù¢
      this.$router.push({ name: "top", params: { userName: this.userName } });
      // ÁôªÂΩï‰ø°ÊÅØ‰øùÂ≠òÂà∞ÁºìÂ≠ò‰∏≠
      sessionStorage.setItem("loginFlag", true);
    },
  },
  created: function () {
    console.log("created");
    this.$emit("header", false);
    var loginFlag = sessionStorage.getItem("loginFlag");
    if (loginFlag != null && loginFlag) {
      // Ë∑≥ËΩ¨Âà∞topÁîªÈù¢
      this.$router.push({ name: "top", params: { userName: this.userName } });
    }
    console.log("created over");
  },
};
</script>
<style src="@/assets/css/login.css"></style>



