          </thead>
          <tbody v-show="questionsArray.length == 0">
            <tr>
              <!-- <td v-if="yourName == ''" class="text-center nodata-message" colspan="4">ËØ∑Â°´ÂÜô‰Ω†ÁöÑÂßìÂêç„ÄÇ</td>
              <td v-else class="text-center nodata-message" colspan="4">{{yourName}}Ôºå‰Ω†Â∑≤ÁªèÂáÜÂ§áÂ•Ω‰∫ÜÂêóÔºü</td>-->
              <td class="text-center nodata-message" colspan="4">
                Âº†ÂòâÁ®ãÔºå‰Ω†Â∑≤ÁªèÂáÜÂ§áÂ•Ω‰∫ÜÂêóÔºü
