      </button>
      <button
        v-show="overFlag"
        type="button"
        class="btn btn-info btn-lg calc-btn"
        data-toggle="modal"
        data-target="#myModal"
        :disabled="isCalced"
      >
        ËÆ°ÁÆóÊú¨Ê¨°‰ΩúÁ≠îÂ•ñÊÉ©
      </button>

      <!-- Modal -->
      <div
